#fix-image-container {
    position: relative;
    z-index: 0;
}

.fixed-img {
    position: fixed;
    overflow: hidden;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
}

.fixed-img img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    min-width: 50%;
    min-height: 50%;
}

.feature {
    position: relative;
    background: #fefefe;
}

.fake-img {
    position: absolute;
    width: 100%;
}

@media (min-width: 768px) {
    .feature {
        margin: 45% 0 0 0;
        min-height: 40%;
    }

    .feature .img-responsive {
        height: 150px;
        margin-top: 35px;
        margin-bottom: 35px;
    }
}

@media (min-width: 992px) {
    .feature {
        margin: 45% 0 0 0;
        min-height: 40%;
    }

    .feature .img-responsive {
        height: 200px;
        margin-top: 35px;
        margin-bottom: 35px;
    }
}

@media (max-width: 768px) {
    .feature {
        margin: 50% 0 0 0;
        min-height: 30%;
    }

    .feature .img-responsive {
        height: 150px;
        margin-top: 15px;
        margin-bottom: 15px;
    }
}

#phone {
    border-bottom: none;
}
